import React from "react";
import { Group, Div, Button, Panel, PanelHeader } from "@vkontakte/vkui";
import connect from "@vkontakte/vk-bridge";
import "./main.css";
import axios from "axios";

let user_id = "///";
let balance = "///";
let bonusbalance = "///";
let user_data_name = "///";
let user_data_name2 = "///";
async function get() {
  let user_info = await connect.sendPromise("VKWebAppGetUserInfo");
  user_data_name = user_info.first_name;
  user_data_name2 = user_info.last_name;
  user_id = Number(user_info.id);
  let getbal = await axios({
    method: "POST",
    url: `https://lucky-coin.ru/user/balance`,
    headers: {
      "Content-Type": "application/json",
    },
    data: {
      id: Number(user_id),
    },
  });
  balance = getbal.data.response.balance;
  bonusbalance = getbal.data.response.bonusbalance;
}
get();
const main = (props) => (
  <Panel id={props.id}>
    <PanelHeader>Профиль</PanelHeader>
    <br></br>
    ID: {user_id}
    <br></br>
    Имя: {user_data_name}
    <br></br>
    Фамилия: {user_data_name2}
    <br></br>
    Баланс: {parseFloat(balance).toFixed(0)}
    <br></br>
    Бонусный баланс: {parseFloat(bonusbalance).toFixed(0)}
    <Button size="xl" level="2" onClick={() => get()}>
      Обновить
    </Button>
  </Panel>
);

export default main;
